  float: right;
  background: transparent;
  margin-top: 0px;
  margin-bottom: 0;
  font-size: 12px;
  padding: 7px 5px;
  position: absolute;
  top: 15px;
  right: 10px;
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  border-radius: 2px;
}
.breadcrumb-alt {
  overflow: hidden;
  list-style: none;
  margin: 0px 0px 20px;
  padding: 0px;
  width: 100%;
}
.breadcrumb-alt > li {
float: left;
margin: 0px 25px 0px 0px;
}
.breadcrumb-alt > li > a {
position: relative;
float: left;
background-color: rgb(238, 238, 238);
color: rgb(118, 118, 118);
font-size: 12px;
padding: 10px;
}
.breadcrumb-alt > li > a::before {
position: absolute;
top: 50%;
left: -1em;
margin-top: -1.6em;
border-color: rgb(238, 238, 238) rgb(238, 238, 238) rgb(238, 238, 238) rgba(0, 0, 0, 0);
border-style: solid;
border-width: 1.5em 0px 1.7em 1em;
content: '';
}
.breadcrumb-alt > li > a::after {
position: absolute;
top: 50%;
right: -1em;
margin-top: -1.5em;
border-bottom: 1.5em solid rgba(0, 0, 0, 0);
border-left: 1em solid rgb(238, 238, 238);
border-top: 1.5em solid rgba(0, 0, 0, 0);
content: '';
}
